footer{
	background: url("../img/bg_footer.png") no-repeat center center;
	background-attachment: fixed;
	background-size: cover;
	min-height: 100vh;
	text-align: center;
	h4{
		padding-top: 50px;
		margin:0; 
	}
	.form_group{
		margin-top: 15px;
		@include media('landscape'){
			display: flex;
			}
	}
	input[type='text']{
		margin-right: 30px;
	}
	input[type='email'],input[type='text']{
		margin-top: 25px;
		width: 100%;
		@include media('>=568px','landscape'){
			width: 50%;
			}
	}
	textarea{
		width: 100%;
		min-height: 130px;
		margin-top: 25px;
		margin-bottom: 45px;
	}
	.btn{
		background-color: $gray;
		border: 2px solid $gray;
		box-shadow: none;
		min-width: 230px;
		outline: none;
		&:hover{
			background-color: $white;
		}
	}
}
