#catalog{
	position: relative;
	border-top: solid 4px #ededed;
	border-bottom: solid 4px #ededed;
	padding-top: 30px;
	padding-bottom: 130px;
	&:before{
		content: "";
		position: absolute;
		top: -41px;
		left: 50%;
		margin-left: -39px;
		background: url("../img/circle.png");
		width: 79px;
		height: 74px;
	}
	&:after{
		content: "";
		position: absolute;
		bottom: -37px;
		left: 50%;
		margin-left: -39px;
		background: url("../img/circle.png");
		width: 79px;
		height: 74px;
	}
	.item_card{
		margin-top: 100px;
		text-align: center;
		@include media('>=568px','landscape'){
		display: flex;
		}
		.item{
			img:not(.dash){
				padding-bottom: 50px;
			}
			@include media('>=568px','landscape'){
			width: 50%;
				img:not(.dash){
					padding-right: 20px;
				}
			}
			p{
				text-align: justify;
				padding-bottom: 30px;
			}
		}
		a{
			background-color: $gray;
			border: solid 2px $gray;
			&:hover{
				background-color: $white;
			}
			
		}
	}
	.dflex{
		margin-bottom: 30px;
		@include flexbox;
		justify-content: space-around;
		p{
			margin-left: 5px;
		}
	}
}

.dash{
	position: relative;
	top: -22px;
}
.order{
	@include media('>=568px','landscape'){
		order: 1;
		img{
			padding-right: 0;
			padding-left: 20px;
		}
		}
	
}