#advantage{
	padding-top: 100px;
	margin-bottom: 100px;
	.dflex{
		@include flexbox;
		flex-wrap: wrap;
		align-items: baseline;
		@include media ('landscape'){
			flex-wrap: nowrap;
			align-items: baseline;
		}
		.item{
			text-align: center;
			margin-top: 60px;
			width: 50%;
			img{
				width: 50%;
				@include media ('landscape'){
					width: auto;
				}
			}
		}
	}
}

#advantage .dflex :first-child img{
	padding-bottom: 11px;
}