#top{
	 transform: translate(-50%, -50%);
  	position: absolute; 
  	top: 50%;
  	left: 50%;
  	width: 90%; 
	text-align: center;
	h1{
		margin-top: 35px;
		margin-bottom: 45px;
	}
	a{
		&:hover{
			background-color: $gray;
		}
	}

	.dflex{
		@include media('>=568px','landscape'){
			@include flexbox;
			flex-direction: row-reverse;
			align-items: center;
			
		}
	}
}
.vh{
	position: relative;
	height: calc(100vh - 50px)
}