/*  -------------------------------------------------
    Fonts styles
    ------------------------------------------------- */

@font-face {
    font-family: 'christmas_scriptcregular';
    src: url('../fonts/christmas_scriptc-webfont.eot');
    src: url('../fonts/christmas_scriptc-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/christmas_scriptc-webfont.woff2') format('woff2'),
         url('../fonts/christmas_scriptc-webfont.woff') format('woff'),
         url('../fonts/christmas_scriptc-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}

