/* 	-------------------------------------------------
	Header styles
 	------------------------------------------------- */
header{
	position: relative;
	background: url("../img/bg_header.png") no-repeat 0 0;
	background-attachment: fixed;
	background-size: cover;
	min-height: calc(100vh + 50px);
	&:before{
		content: "";
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 50px;
		background: linear-gradient(to top, $white 30%, transparent 100%);
	}
	#nav{
		@include flexbox;
		justify-content: space-between;
		align-items: center;
		a{
			position: relative;
			&:after{
				position: absolute;
				content: "";
				width: 28px;
				height: 28px;
				left: -30px;
				top:50%;
				margin-top: -14px;
				background: url("../img/pigeon.png");
			}
		}
	}
	#scroll{
		position: absolute;
		width: 31px;
		height: 83px;
		background: url("../img/scroll_down.png");
		cursor: pointer;
		bottom: -20px;
		left: 50%;
		margin-left: -15px;
	}
}
