// Colors
// Color calculator - http://razorjam.github.io/sasscolourfunctioncalculator/
// Color names - http://name-of-color.com/

$gray: #ddd;
$black: #000;
$white: #fff;

$base-font-logo: 'christmas_scriptcregular', normal, normal;
$base-font-bold: 'pt_serifbold', normal, normal;
$base-font-reg: 'pt_serifregular', normal, normal;
$header-font-reg: 'loraregular', normal, normal;
$header-font-bold: 'lorabold', normal, normal;

// Body
$base-text-color: $black;
$base-background-color: $white;
$font-size-base: 16px;
$line-height-base: 1.2 !default;
$base-font-family: $base-font-reg !default;

$vertical-rhythm: ($line-height-base * 1em);

$base-min-width: 320px;
// Liks
$base-link-color: $black;

// Buttons 
$btn-color: $white;
$btn-background: $gray;
$btn-padding: 15px 0 15px 15px;
$btn-font-size: inherit !default;

// Forms
$form-element-padding: $btn-padding !default;
$form-element-border-color: $gray !default;
$form-element-focus-border-color: $black !default;
$placeholder-color: $gray !default;

// Headers
$h1-font-size: 30px !default;
$h2-font-size: 55px;
$h3-font-size: 24px !default;
$h4-font-size: 36px !default;
$h5-font-size: 17px !default;
$h6-font-size: 15px !default;

$headings-font-family: $base-font-bold;
$headings-color: $black;
$logo-font-size: 32px !default;

$animation-speed: 0.3s;
.big{
	font-size: 25px;
}