#testimonials{
	position: relative;
	padding-bottom: 130px;
	border-bottom: solid 4px #ededed;
	&:after{
		content: "";
		position: absolute;
		bottom: -37px;
		left: 50%;
		margin-left: -39px;
		background: url("../img/circle.png");
		width: 79px;
		height: 74px;
	}
	h4{
		padding-bottom: 60px;
	}
	.item_card{
		padding-left: 20px;
		padding-right: 20px;

	}
}
.item_img{
	text-align: center;
	margin-bottom: 20px;
	img{
		display: inline-block;
	}
	span{
		position: relative;
		&:before{
		position: absolute;
		content: "";
		left: 120px;
		background: url("../img/quote.png") no-repeat 0 0;
		background-size: contain;
		width: 50px;
		height: 50px;
		z-index: 2;
		}
	}

}
.item_desc>:last-child{
	text-align: right;
	span{
		position: relative;
		&:before{
			position: absolute;
			content: "";
			width: calc(100% + 5px);
			height: 2px;
			background-color: $gray;
			top:-10px;
			left: -2px;
		}
	}
}
.slick-slide{
	&:focus{
		outline: none;
	}

}